<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido(data-aos="flip-up")
      .titulo-principal__numero
        span 7
      h1 Normativas de conservación del patrimonio y reglamentos de turismo sostenible
  
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-up")
      p El turismo sostenible no se fundamenta únicamente en la buena voluntad de los actores involucrados, sino en la existencia de marcos normativos claros que orienten las acciones de gobiernos, empresarios y comunidades hacia prácticas responsables. Estas leyes, políticas y reglamentos constituyen una guía esencial para la gestión del turismo, al establecer los criterios necesarios para garantizar la protección del patrimonio natural y cultural, promover el uso racional de los recursos y asegurar que la actividad turística genere beneficios sostenibles sin comprometer el bienestar de las generaciones futuras.
      p
      h3 Normativas de conservación del patrimonio
      p La conservación del patrimonio cultural y natural es uno de los pilares fundamentales del turismo sostenible, ya que permite preservar los valores, símbolos y recursos que conforman la identidad de un país. En Colombia, este compromiso se materializa a través de un marco legal robusto que articula políticas nacionales con compromisos internacionales, orientado a proteger los bienes tangibles e intangibles que enriquecen la memoria colectiva y promueven el desarrollo responsable del turismo
      p 
      h3 Marco nacional 
      p En el contexto colombiano, la Ley 397 de 1997, conocida como Ley General de Cultura, constituye el principal instrumento normativo para la protección, conservación y difusión del patrimonio cultural. Esta ley define el patrimonio como el conjunto de bienes materiales e inmateriales que expresan la creación, historia y diversidad cultural de la Nación. Asimismo, asigna al Ministerio de las Culturas, las Artes y los Saberes la responsabilidad de coordinar y supervisar las acciones de conservación y promoción del patrimonio.
      p Gracias a esta legislación, Colombia cuenta con una clasificación oficial de Bienes de Interés Cultural (BIC), que incluye desde sitios arqueológicos, monumentos y centros históricos, hasta manifestaciones vivas como festividades, músicas tradicionales, saberes y expresiones artísticas.
      p
      h3(data-aos="fade-down") Ejemplos destacados de lugares declarados BIC

      br
      br
      .row.justify-content-center(data-aos="fade-up")
        .col-sm-6.col-lg-9.col-xl-3.mb-4.mb-xl-0
          .tarjeta.tarjeta-slide.arriba.color-secundario(@mouseover="indicadorTarjetaSlide = false")
            .indicador--hover(v-if="indicadorTarjetaSlide")
            .tarjeta-slide__contenedor
              .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/temas/tema1/tema1-ce-tarjeta10.svg')})`}")
              .tarjeta-slide__contenido.p-4.p-xl-5
                p Centro Histórico de Cartagena de Indias, representativo del patrimonio colonial y uno de los destinos turísticos más visitados del país.

        .col-sm-6.col-xl-3.mb-4.mb-xl-0(data-aos="flip-left")
          .tarjeta.tarjeta-slide.abajo.color-acento-contenido(@mouseover="indicadorTarjetaSlide = false")
            .tarjeta-slide__contenedor
              .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/temas/tema1/tema1-ce-tarjeta11.svg')})`}")
              .tarjeta-slide__contenido.p-4.p-xl-5
                p Paisaje Cultural Cafetero, declarado en 2011 por su integración entre la producción agrícola, la arquitectura tradicional y la cultura cafetera.

        .col-sm-6.col-xl-3.mb-4.mb-sm-0(data-aos="flip-right")
          .tarjeta.tarjeta-slide.derecha.color-acento-botones(@mouseover="indicadorTarjetaSlide = false")
            .tarjeta-slide__contenedor
              .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/temas/tema1/tema1-ce-tarjeta12.svg')})`}")
              .tarjeta-slide__contenido.p-4.p-xl-5
                p Carnaval de Barranquilla, Semana Santa de Popayán y Música de marimba y cantos del Pacífico Sur, reconocidos como expresiones del patrimonio cultural inmaterial.

      p
      br
      .row.justify-content-center.align-items-center.mb-4(data-aos="fade-up")
        p La Ley 1185 de 2008 complementó la Ley 397 al fortalecer los procedimientos de identificación, registro y manejo del patrimonio cultural, ampliando la participación de las comunidades locales y promoviendo su apropiación social como garantía de sostenibilidad. De igual forma, normas como el Decreto 763 de 2009 y el Plan Nacional de Cultura 2020–2030 han reforzado el enfoque territorial y participativo en la gestión del patrimonio.
        h3 Marco internacional
        p En el plano internacional, la Convención sobre la Protección del Patrimonio Mundial Cultural y Natural, adoptada por la UNESCO en 1972, es uno de los instrumentos más influyentes en materia de conservación. Este acuerdo reconoce que ciertos lugares poseen un valor universal excepcional, lo que obliga a los Estados Partes a garantizar su protección, conservación y transmisión a las generaciones futuras.
        p Colombia ratificó esta convención en 1983, y desde entonces varios de sus sitios han sido inscritos en la Lista del Patrimonio Mundial, entre ellos:

      br
      p
      .row.justify-content-center(data-aos="fade-down")
        .col-sm-6.col-lg-9.col-xl-3.mb-3.mb-xl-0
          .tarjeta.tarjeta-slide.arriba.color-primario(@mouseover="indicadorTarjetaSlide = false")
            .tarjeta-slide__contenedor
              .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/temas/tema1/tema1-ce-tarjeta13.svg')})`}")
              .tarjeta-slide__contenido.p-4.p-xl-5
                p Parque Arqueológico de San Agustín, destacado por su conjunto de esculturas y monumentos megalíticos precolombinos.

        .col-sm-6.col-lg-9.col-xl-3.mb-3.mb-xl-0(data-aos="fade-right")
          .tarjeta.tarjeta-slide.abajo.color-secundario(@mouseover="indicadorTarjetaSlide = false")
            .indicador--hover(v-if="indicadorTarjetaSlide")
            .tarjeta-slide__contenedor
              .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/temas/tema1/tema1-ce-tarjeta14.svg')})`}")
              .tarjeta-slide__contenido.p-4.p-xl-5
                p Parque Nacional Natural Los Katíos, declarado Patrimonio Natural por su biodiversidad y su papel en la conexión biogeográfica entre América Central y del Sur.

        .col-sm-6.col-xl-3.mb-3.mb-xl-0(data-aos="fade-left")
          .tarjeta.tarjeta-slide.derecha.color-acento-contenido(@mouseover="indicadorTarjetaSlide = false")
            .tarjeta-slide__contenedor
              .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/temas/tema1/tema1-ce-tarjeta15.svg')})`}")
              .tarjeta-slide__contenido.p-4.p-xl-5
                p Puerto, fortificaciones y conjunto monumental de Cartagena de Indias, ejemplo emblemático de la arquitectura militar colonial.

        .col-sm-6.col-xl-3.mb-3.mb-sm-0(data-aos="flip-up")
          .tarjeta.tarjeta-slide.izquierda.color-acento-botones(@mouseover="indicadorTarjetaSlide = false")
            .tarjeta-slide__contenedor
              .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/temas/tema1/tema1-ce-tarjeta16.svg')})`}")
              .tarjeta-slide__contenido.p-4.p-xl-5
                p Centro Histórico de Santa Cruz de Mompox, testimonio vivo de la herencia cultural del río Magdalena.

      p
      .row.justify-content-center.align-items-center.mb-4(data-aos="fade-up")
        p Además de la Convención de 1972, Colombia ha ratificado otros instrumentos internacionales relevantes, como:
        .col-md-9.mb-5.mb-md-0
          p La Convención para la Salvaguardia del Patrimonio Cultural Inmaterial (UNESCO, 2003).
          p La Convención sobre la Protección y Promoción de la Diversidad de las Expresiones Culturales (UNESCO, 2005).
      p(data-aos="fade-up") Ambas refuerzan la necesidad de proteger las manifestaciones vivas y los modos de vida tradicionales frente a los efectos de la globalización y la masificación turística.
      p 
      h3(data-aos="fade-down") Patrimonio y turismo sostenible
      p(data-aos="flip-up") La aplicación efectiva de los reglamentos en materia de turismo sostenible es esencial para equilibrar la conservación del patrimonio y el desarrollo turístico. Estos marcos normativos orientan la planificación, operación y evaluación de las actividades turísticas, asegurando que se respeten los valores culturales, ambientales y sociales de cada territorio.
      p(data-aos="fade-down") En Colombia, la normativa nacional —como la Ley General de Turismo (Ley 300 de 1996 y sus modificaciones), el Decreto 1074 de 2015 y la Política de Turismo Sostenible—establece directrices claras para promover un turismo responsable, inclusivo y competitivo. Estas disposiciones buscan minimizar los impactos negativos sobre losecosistemas, fomentar la participación comunitaria y fortalecer el aprovechamiento sostenible del patrimonio natural y cultural.
      p(data-aos="fade-left") Los destinos patrimoniales deben gestionarse bajo principios de sostenibilidad, evitando la sobreexplotación de los recursos culturales y naturales, y garantizando que las comunidades locales sean beneficiarias directas de la actividad turística. De esta manera, el cumplimiento de la normativa no solo protege los bienes patrimoniales, sino que también impulsa modelos de gobernanza participativa, donde la comunidad desempeña un papel activo en la toma de decisiones.
      p(data-aos="fade-up") Además, un turismo basado en el respeto al patrimonio fomenta la educación, la identidad y la cohesión social, y contribuye a la proyección internacional del país. En consonancia con lo señalado por la UNESCO (2018), “la protección del patrimonio cultural y natural no es un obstáculo al desarrollo, sino una oportunidad para construir un futuro más sostenible e inclusivo”. Este enfoque integral convierte a las normas en herramientas estratégicas para asegurar que el turismo contribuya efectivamente al desarrollo humano, la equidad social y la conservación ambiental.

